.menu{background-color:rgba(16,55,92,0.9);z-index:2;width:70%;color:white;position:absolute;top:70px;bottom:70px;-webkit-animation:menuAnimation .5s;animation:menuAnimation .5s;right:0}.menu ul{padding:0;margin:0;list-style:none;width:100%}.menu ul li{width:100%;padding:20px 0;text-align:right}.menu ul li a,.menu ul li span{padding:15px 20px !important;margin:0;width:100%;color:white;text-decoration:none;border-bottom:none;font-family:Arial, Helvetica, sans-serif}@-webkit-keyframes menuAnimation{from{top:70px;bottom:70px;right:0;opacity:0;width:0%}to{top:70px;bottom:70px;right:0;width:70%;opacity:1}}@keyframes menuAnimation{from{top:70px;bottom:70px;right:0;opacity:0;width:0%}to{top:70px;bottom:70px;right:0;width:70%;opacity:1}}

body{font-family:'MuseoModerno', cursive}header{background-color:#10375c;margin:0;padding:0;width:100%;height:70px;display:flex;justify-content:space-between;align-items:center}header h1{margin:0;padding:0 20px;color:#f3c623}header span{margin:0 20px 0 0;color:white;font-weight:normal;border-bottom:0.5px solid white;padding-bottom:3px;font-family:Arial, Helvetica, sans-serif;cursor:pointer;transition:.5s;will-change:transform}header span:active{transform:scale(0.9)}footer{margin:0;padding:0;display:flex;justify-content:center;align-items:center;background-color:#10375c;height:70px}footer p{margin:0;padding:0 20px;color:#f3c623}footer p a{margin:0 10px;color:white}

.mapContainer{width:100%;height:calc(100vh - 140px);overflow:hidden;margin:0;padding:0;position:relative}.map{height:calc(100vh - 140px) !important;width:100% !important}.modalContainer{background-color:rgba(243,198,35,0.5);position:absolute;z-index:3;top:0;left:0;right:0;bottom:0;-webkit-animation:modalAnimation 1.5s;animation:modalAnimation 1.5s;display:flex;justify-content:center;align-items:center}.modalContainer-box{background-color:#f4f6ff;width:80%;margin:auto;max-width:300px;border-radius:5px;border:none;display:flex;justify-content:center;align-items:center;flex-direction:column;padding:15px 0}.modalContainer-box p{font-weight:bold;text-align:center;color:#10375c}.modalContainer-box input{border:1px solid #10375c;margin:0 auto 20px auto;border-radius:25px;padding:5px;outline:none;padding:5px 15px}.modalContainer-box input::-webkit-input-placeholder{padding:0 5px}.modalContainer-box input:-ms-input-placeholder{padding:0 5px}.modalContainer-box input::-ms-input-placeholder{padding:0 5px}.modalContainer-box input::placeholder{padding:0 5px}.modalContainer-box button{border-radius:25px;border:none;outline:none;transition:.5s;margin:0 10px 20px 10px;padding:5px 20px}.modalContainer-box button:active{transform:scale(0.9)}.modalContainer-box-confirm{background-color:#127681;color:#f4f6ff}.modalContainer-box-cancel{color:#10375c}.popup{padding:5px}.popup-text{font-weight:bold}@-webkit-keyframes modalAnimation{from{opacity:0}to{top:0;bottom:0;right:0;left:0;opacity:1}}@keyframes modalAnimation{from{opacity:0}to{top:0;bottom:0;right:0;left:0;opacity:1}}

.containerPlaces{display:flex;justify-content:center;align-items:center;flex-direction:column}.containerPlaces-list{width:100%;max-height:40vh;display:flex;flex-direction:column}.containerPlaces-list h1{font-size:20px;text-align:center;padding:10px 0;color:#10375c;display:flex;justify-content:center;align-items:center}.containerPlaces-list h1 img{width:25px;margin:0 5px 0 0;padding:0}.containerPlaces-list-box{width:100;overflow:scroll;display:flex;justify-content:center;align-items:center;flex-wrap:wrap;margin-bottom:20px;overflow-x:hidden;overflow-y:auto}.containerPlaces-list-box img{width:70%;margin:auto;max-width:150px}.containerPlaces-list-box p{text-align:center;padding:0 20px}.containerPlaces-list-box span{background-color:rgba(16,55,92,0.7);color:#f4f6ff;border-radius:5px;padding:7px 10px;margin:5px;width:90px;text-overflow:ellipsis;white-space:nowrap;overflow:hidden;will-change:transition;transition:.5s;text-align:center;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.containerPlaces-list-box span:active{transform:scale(0.9)}.containerPlaces-list-box::-webkit-scrollbar{width:8px}.containerPlaces-list-box::-webkit-scrollbar-track{box-shadow:inset 0 0 5px gray;border-radius:10px}.containerPlaces-list-box::-webkit-scrollbar-thumb{background:#10375c;border-radius:10px}.containerPlaces-map{width:100%}@media (min-width: 600px){.containerPlaces{flex-direction:row;padding:0 0;align-items:flex-start}.containerPlaces-list{width:400px;max-height:78vh;margin-bottom:0}.containerPlaces-list h1{font-size:16px}.containerPlaces-list-box{overflow-x:hidden;overflow-y:auto;margin-bottom:0;height:100%}.containerPlaces-list-box img{width:70%;margin:auto;max-width:100px}.containerPlaces-list-box span{background-color:#f4f6ff;color:#10375c;width:95%;border-radius:0px;margin:0;padding:20px;text-align:left;border-bottom:1px solid rgba(16,55,92,0.3);cursor:pointer}.containerPlaces-list-box span::before{content:'â€¢ ';font-weight:bold}.containerPlaces-list-box span:hover{background-color:#10375c;color:white}.containerPlaces-list-box span:last-child{border-bottom:none}.containerPlaces-map{overflow:hidden;padding:0}}

.containerNotFound{min-height:calc(100vh - 140px);margin:0;padding:0;display:flex;justify-content:center;align-items:center;background-color:#1B1B2E;flex-direction:column}.containerNotFound img{width:70%;margin:0;padding:0}.containerNotFound h1{color:white;max-width:400px;text-align:center;font-size:20px;width:70%}.containerNotFound a{background-color:#127681;color:white;padding:10px;border-radius:5px;text-decoration:none;will-change:transform;transition:.5s}.containerNotFound a:active{transform:scale(0.9)}@media (min-width: 900px){.containerNotFound img{width:30%}}

